@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,400;0,700;1,400&display=swap);
html {
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
}

*, ::after, ::before {
    box-sizing: inherit;
    -webkit-font-smoothing: inherit;
}

body {
    font-family: 'Dela Gothic One', cursive;
    background-color: #005857;
    margin: 0px;
    background-image: url(/converter-react/static/media/doodle.12130957.jpg);
    background-size: 750px;
    background-position: center;
    background-attachment: fixed;
}

a {
    text-decoration: none;
}
/* converter.css */

.converter {
    flex-basis: 400px;
    margin: 30px;
    text-align: center;
    font-size: 20px;
    color: #005857;
    background-color: #FFF;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px 10px #00000099;
}

.converter--hidden {
    display: none;
}

.converter__header {
    padding: 0 0 20px 0;
}

.converter__select {
    font-family: 'Dela Gothic One', cursive;
    width: 150px;
    text-align: center;
    border: #000 solid 2px;
    border-radius: 10px;
    padding: 10px;
    background-color: #FFF;
    color: #005857;
}

.converter__select:active {
    background-color: #005857;
    color: #FFF;
}

.converter__select:focus {
    background-color: #005857;
    color: #FFF;
}

.converter__field {
    font-family: 'Dela Gothic One', cursive;
    width: 250px;
    text-align: center;
    border: #000 solid 2px;
    border-radius: 10px;
    padding: 10px;
    background-color: #FFF;
    color: #005857;
}

.converter__field:active {
    background-color: #ebebeb;
}

.converter__field:focus {
    background-color: #def0ef;
    color: #000;
}

.converter__button {
    display: block;
    font-size: 20px;
    margin: 15px auto;
    border: 2px solid;
    border-radius: 10px;
    padding: 7px;
    color: #005857;
    background-color: hsl(179, 100%, 98%);
    transition: 0.3s;
}

.converter__button:hover {
    background-color: hsl(179, 100%, 20%);
    color: #FFF;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
}

.converter__code {
    color: #FFF;
    background-color: #005857;
    font-size: 18px;
    padding: 7px;
    border-radius: 10px;
    margin-left: 10px;
}
/* header.css */
.header__header {
    text-align: center;
    color: #FFF;
    text-shadow: 0 0 10px #000;
}
/* list */

.list {
    max-width: 98%;
    flex-basis: 350px;
    text-align: center;
    font-size: 16px;
    color: #005857;
    background-color: #FFF;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px 10px #00000099;
    margin-top: 30px;
    margin-bottom: 50px;
}

.list__header {
    padding: 0 0 10px 0;
    font-size: 30px;
}

.list__savedList {
    list-style-type: none;
    margin: auto;
    padding: 0px;
}

.list__savedPositions {
    color: #000;
    border-bottom: 1px #CCC solid;
    border-top: 1px #AAA solid;
    padding: 20px;
    margin: auto;
    line-height: 2.3;
}

.list__savedPositions:hover {
    background-color: #def0ef;
}

.list__code {
    color: #FFF;
    background-color: #005857;
    font-size: 12px;
    padding: 5px;
    border-radius: 10px;
}
.box{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    margin: 0;
    padding: 0;
}
